#header {
  h1 a {
    text-decoration: none;
    border: 0px;
  }
}

#writing h3 {
  margin-bottom: 12px;
}

.content {
  display: block;
  overflow-wrap: normal;
  margin-bottom: 16px;

  p {
    margin-top: 0px;
    margin-bottom: 16px;
    text-align:left;
  }
  code {
    color: $color-accent-1;
    padding: 0 5px;
    border: 0.5px solid $color-border;
    border-radius: 2px;
  }
  a {
    color: $color-accent-1 !important;
    text-decoration: none;
    border-bottom: 0.5px solid $color-link;

    &:hover {
      background-image: none;
      background-color: $color-link-hover;
      border-bottom: solid 0.5px $color-link;
      color: $color-accent-2;
    }
  }

  blockquote {
    border-left: $color-quote solid 4px;
    margin-right: 32px;
    background-color: lighten($color-quote, 47%);

    p {
      margin-left: 8px;
      margin-bottom: 0px;
    }
  }

  h1, h2, h3 {
    margin-top: 8px;
    margin-bottom: 8px;
  }
  h2 {
    color: $color-accent-1;
  }
  h2 a {
    text-decoration: none;
    border: 0px;
    &:hover{
      background-color:$color-background;
      border-bottom: 0.5px solid $color-link;
    }
  }
  ul {
    margin-top: 8px;
    margin-bottom: 8px;
  }
  .icon {
    font-size: 1.3rem;
    vertical-align: bottom;
    margin-right: 1px;
    margin-left: 1px;
  }
  a.icon {
    background: none;
    border: 0px;
    color: $color-accent-1 !important;

    &:hover {
      color: $color-accent-2 !important;
      border: 0px;
    }
  }

  .footnotes {
    font-size: 0.875rem;
    color: $color-meta !important;
  }
   hr {
     border: 0.25px solid $color-accent-1;
   }
}

#privacy-statement {
  border: solid $color-accent-2 0.5px;
  border-radius: 4px;
  padding: 4px 8px 8px 8px;
  margin-bottom: 24px;
  background-color: lighten($color-accent-2, 37%);
  font-size: 0.875rem;
  line-height: 1.2rem;
}

.privacy-statement  h4 {
    text-align: left;
    margin: 0 0 1px 0;
  }

.privacy-statement  p {
    margin: 0px;
  }


pre {
  overflow-x: auto;
  padding: 15px 15px 10px 15px;
  border: 1px dotted $color-border;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  font-size: 1rem;
  font-family: $font-family-mono;
  line-height: 22px;
  position: relative;

  .code-copy-btn {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    border-radius: 0 2px;
    padding: 0;
    font-family: $font-family-mono;
    font-weight: 800;
    font-size: 0.9em;
    line-height: 1.7;
    color: #fff;
    background-color: #8c8c8c;
    min-width: 60px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 0em;
  }

  .code-copy-btn:hover {
    background-color: #666;
    color: #2bbc8a;
  }

  code {
    display: block;
    padding: 0;
    border: none;
    background-color:$color-text;
  }
}

code {
  font-family: $font-family-mono;
  padding: 0 5px;
  border: 0.5px solid $color-border;
  border-radius: 2px;
  -webkit-border-radius: 2px;
}

#header {
  #nav {
    ul {
      li {
        border-right: 0.5px solid $color-text;
        a {
          color: $color-link;
          font-size: 1rem;
          font-weight: 500;
          text-decoration: none;
          border-bottom: 0px;
          }
        a:hover{
          background-color: $color-link-hover;
          border-bottom: 0.5px solid $color-text;
          }
        }
      }
    }
  }

#index-categories
  .category-list-title {
    padding-left: 0px;
  }
  .category-list {
    .category-list-item {
      list-style-type: none;
      text-align:left;
      .category-list-count {
        color: $color-meta;
      }
      .category-list-count:before {
        content: " (";
      }
      .category-list-count:after {
        content: ")";
      }
    }
  }

article {
  header {
    .posttitle {
      margin-top: 0;
      margin-bottom: 0;
      text-transform: none;
      font-size: 1.5em;
      line-height: 1.25;
    }
    .meta {
      margin-top: 0;
      margin-bottom: 1rem;
      color: $color-meta;
      font-size: .85rem;
    }
    .meta a {
      border-bottom: 0px;
    &a:hover {
      border-bottom: 0px;
      color: $color-link;
      }
    }
    .author {
      text-transform: uppercase;
      letter-spacing: .01em;
      font-weight: 700;
    }
    .postdate {
      display: inline;
    }
  }
}
